.loading-icon {
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.loading-icon-container {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: colors(offwhite);
}



