@mixin small() {
  @media screen and (min-width: breakpoints(small)) {
    @content;
  }
}

@mixin medium() {
  @media screen and (min-width: 500px) {
    @content;
  }
}

@mixin large() {
  @media screen and (min-width: breakpoints(large)) {
    @content;
  }
}



